.App {
    font-family: sans-serif;
}

.products {
    margin: 20px;
    padding: 0;
    display: grid;
    gap: 20;
    grid-template-columns: 1fr 1fr 1fr;
}

.products__single {
    height: 250px;
    padding: 20px;
    margin: 20px;
    background-color: rgb(220, 220, 220);
    text-align: center;
    border-radius: 5px;
    cursor: pointer;
}

.products__single > img {
    width: 100%;
    height: 95%;
    object-fit: cover;
    margin-bottom: 3px;
}

.pagination {
    padding: 10px;
    margin: 15px 0;
    display: flex;
    justify-content: center;
}

.pagination > span {
    padding: 15px 20px;
    border: 1px solid gray;
    cursor: pointer;
}

.pagination > span:hover {
    text-decoration: underline;
    background-color: rgb(220, 220, 220);
}

.pagination__selected {
    background-color: rgb(220, 220, 220);
    text-decoration: underline;
}